* {
  font-family: "Roboto", sans-serif;
  font-weight: 300 !important;
}
/* .mdl-cell {
  margin: 0;
}*/
.mdl-grid {
  margin: 0;
}
.mdl-card {
  position: static !important;
}
.react-reveal {
  animation-fill-mode: none !important;
}
.fadeIn {
  opacity: 0;
  margin-top: 300px;
  max-width: 100%;
}

/* main img */
.mainimg {
  background-image: url("./image/main.jpg");
  height: 100vh;
  width: 100%;
  opacity: 0.9;

  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;

  -webkit-background-attachment: fixed;
  -moz-background-attachment: fixed;
  -o-background-attachment: fixed;

  -webkit-background-position: center;
  -moz-background-position: center;
  -o-background-position: center;

  -webkit-background-repeat: no-repeat;
  -moz-background-repeat: no-repeat;
  -o-background-repeat: no-repeat;
}

/* header menu */
#header {
  float: right;
  position: fixed;
  width: 94%;
  top: 0;
  z-index: 9;
}
.header-menu {
  float: right;
  background-color: rgba(0, 0, 0, 0.6);
  color: #fff;
  border-radius: 2px 2px 30px 30px;
}
.header-menu ul li {
  display: inline;
  color: #c4c9bf;
  font-weight: bold;
}
.header-menu ul li:hover {
  color: white;
}
.header-li {
  font-size: 1.3em;
  margin-right: 2em;
  padding: 1em;
  cursor: pointer;
  opacity: 0.8;
}
.active {
  transition: opacity ease-in-out;
  transform: translateY(0px);
}
.hidden {
  transition: opacity ease-in-out;
  transform: translateY(0px);
}

/* main description */
.banner {
  position: relative;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.intro-text {
  background-color: rgba(0, 0, 0, 0.6);
  color: #fff;
  text-align: center;
  border-radius: 10px;
  padding: 15px 0;
}
.intro-text h1 {
  margin-top: 0;
  font-size: 5em;
  padding: 0 3%;
  margin-block-start: 0em;
  margin-block-end: 0.1em;
}
.intro-text hr {
  width: 25%;
  margin: auto;
  border-top: solid 2px;
}
.brief-skills {
  font-size: 1.5em;
  padding: 20px;
}
.header-menu select {
  display: none;
}

/*about me*/
.aboutme {
  width: 85%;
  margin: auto;
  padding: 5% 0;
}

.aboutme-head {
  display: flex; /*여기부터 3개가 좌우 위아래 가운데 맞추는법*/
  justify-content: center;
  align-items: center;
}
.aboutme-head h1 hr {
  display: block;
  height: 2px;
  width: 65px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: auto;
  background: #7b7cd4;
}
.aboutme-head h1 {
  font-size: 4em;
}
.aboutme-underline {
  height: 2px;
  width: 65px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: auto;
  background: #7b7cd4;
}

.aboutme-content p {
  padding: 0;
  margin: 0;
  top: 50%;
  font-size: 1.3em;
}

/* experiences */
.experiences {
  background: #f7f4f4;
}
.experiences-container {
  width: 85%;
  margin: auto;
  padding: 2% 0;
}
.experiences-container h1 {
  text-align: center;
}
.experiences-container h2 {
  color: #343a40;
}
.experiences-title {
  font-size: 2.5em;
  margin-bottom: 0px;
}
.experiences-container h3 {
  color: #868296;
}
.experiences-position {
  font-size: 1.7em;
  margin-top: 12px;
}
.expereinces-detail-container {
  padding: 8px;
}
.experiences-detail {
  font-size: 1.2em;
}
.experiences-date {
  font-size: 1.3em;
  font-weight: bold;
  text-align: right;
  margin-bottom: 0;
}

/* skills */
.skills {
  width: 85%;
  margin: auto;
  padding: 4% 0;
}
.skills-title-content {
  padding-right: 20px;
  display: flex; /*여기부터 3개가 좌우 위아래 가운데 맞추는법*/
  justify-content: center;
  align-items: center;
}
.skills-title hr {
  height: 2px;
  width: 65px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: auto;
  background: #7b7cd4;
}
.graph {
  width: 100%;
  font-size: 1.2em;
}
.graph-item {
  width: 15%;
}
.graph-spacing {
  padding-bottom: 10px;
}
.bar-wrap {
  background-color: #ecf0f1;
  width: 100%;
  color: white;
  border-radius: 5px;
}
.bar {
  /* animation: bar-before 1 1.8s; */
  border-radius: 5px;
  padding: 10px;
}
@keyframes bar-before {
  0% {
    width: 0px;
  }
}
.bar1 {
  background-color: #2ecc71;
  width: 65%;
}
.bar2 {
  width: 50%;
  background-color: #2ecc71;
}
.bar3 {
  width: 55%;
  background-color: #2ecc71;
}
.bar4 {
  width: 70%;
  background-color: #2ecc71;
}
.bar5 {
  width: 60%;
  background-color: #2ecc71;
}
.bar6 {
  width: 25%;
  background-color: #2ecc71;
}
.bar7 {
  width: 30%;
  background-color: #2ecc71;
}

/* projects */
.projects {
  background: #f7f4f4;
  padding: 2% 0 4% 0;
}
.card {
  height: 270px !important;
}
.projects-container {
  width: 65%;
  margin: auto;
}
.projects-head {
  text-align: center;
}

/* footer */
.footer {
  padding: 3% 0;
}
.footer-head {
  text-align: center;
}
.footer-container {
  margin: auto;
  width: 72%;
}
.googlemap {
  position: relative !important;
}
.info-container {
  height: 400px;
}
.map-container {
  display: flex;
  flex-direction: column;
}
.btn {
  font-size: 1.3em !important;
}
.info-container svg {
  font-size: 2.3em;
}

@media (max-width: 1200px) {
  .projects-container {
    width: 75%;
  }
  .footer-container {
    margin: auto;
    width: 72%;
  }

  .btn {
    font-size: 1em !important;
  }
  .info-container svg {
    font-size: 2em !important;
  }
}

/* media */
@media (max-width: 960px) {
  /* nav bar */
  #header {
    position: fixed;
    width: 100%;
  }
  .header-menu {
    width: 100%;
  }
  .header-menu ul {
    padding: 0 50px;
    display: flex;
    justify-content: space-between;
    list-style-type: none;
  }
  .header-menu ul li {
    display: block;
    flex: 0 1 auto; /* Default */
  }
  .header-li {
    font-size: 12px;
    margin-right: 0;
    padding: 0;
    cursor: pointer;
    opacity: 0.8;
  }

  /* main description */
  .intro-text {
    padding: 2% 0;
  }
  .intro-text h1 {
    margin-top: 0;
    font-size: 2.5em;
    padding: 0 3%;
    margin-block-start: 0em;
    margin-block-end: 0.1em;
  }
  .intro-text hr {
    width: 25%;
    margin: auto;
    border-top: solid 2px;
  }
  .brief-skills {
    font-size: 11px;
    padding: 20px;
  }

  /*about me*/
  .aboutme {
    width: 95%;
    padding: 6% 0;
    margin: auto;
  }
  .aboutme-container {
    display: block !important;
  }
  .aboutme-head {
    margin: auto !important;
  }
  .aboutme-head h1 {
    font-size: 3em;
    margin-top: 0;
    margin-bottom: 15px;
  }
  .aboutme-content {
    padding-top: 20px;
    margin: auto !important;
    width: 95% !important;
  }

  /* experiences */
  .experiences-container {
    width: 90%;
    margin: auto;
    padding-top: 2%;
  }
  .experiences-head {
    font-size: 3em;
  }
  .experiences-title {
    font-size: 1.3em;
    line-height: 1.2;
    margin: auto;
  }
  .experiences-position {
    font-size: 1.2em;
    margin-top: 12px;
  }
  .experiences-detail {
    font-size: 1em;
  }
  .experiences-date {
    font-size: 1em;
    font-weight: bold;
    text-align: center;
    margin: auto;
    line-height: 1.3;
  }
  .experiences-head .row {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  /* skills */
  .skills {
    width: 95%;
    margin: auto;
    padding: 4% 0 6% 0;
  }
  .skills-title {
    margin: 10% auto;
  }
  .skills-title-content {
    margin: auto !important;
  }
  .skills-graph {
    padding-top: 15px;
    margin: auto !important;
    width: 95% !important;
  }
  .skills .row {
    display: block;
  }
  .skills-title {
    font-size: 3em;
  }
  .graph {
    width: 100%;
    font-size: 0.8em;
  }
  .graph tbody tr {
    width: 100%;
  }
  .graph-item {
    font-size: 1.2em;
  }
  .graph-spacing {
    padding-bottom: 5px;
  }
  .bar {
    border-radius: 5px;
    padding: 5px;
  }

  /* projects */
  .projects-container {
    width: 95%;
    margin: auto;
  }
  .projects-head {
    font-size: 3em;
  }

  /* contact me */
  .footer-container {
    width: 95%;
  }
  .footer-head {
    font-size: 3em;
  }
  .footer-head {
    margin-bottom: 0;
  }

  .info-container {
    height: 800px;
    display: flex; /*여기부터 3개가 좌우 위아래 가운데 맞추는법*/
    justify-content: center;
    align-items: center;
  }
  .info-container svg {
    font-size: 1.5em !important;
  }
  .btn {
    font-size: 1em !important;
  }
  .contact-container {
    width: 100% !important;
    justify-content: center; /* 여기 2개가 vertical center*/
    display: flex;
  }

  .map-container {
    height: 400px;
    width: 150% !important;
  }
}
